* {
  vertical-align: baseline;
  font-style: normal;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: transparent;
}

body {
  position: relative;
  text-align: left;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  overflow-x: hidden;
  color: #333;
}

input {
  -webkit-appearance: none;
}

em {
  text-decoration: underline;
}

.container {
  display: block;
  margin: auto;
  width: 100%;
  max-width: 1010px;
}

.loader {
  padding: 100px 0;
  font-size: 20px;
  text-align: center;
  color: #ccc;
}

.footer-holder {
  width: 100%;
  height: 62px;
}

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}

.material-icons {
  vertical-align: middle;
}

::-moz-selection {
  color: #fff;
  background: #11aacc;
}

::selection {
  color: #fff;
  background: #11aacc;
}

.center {
  text-align: center;
  margin: auto;
}

.white-text {
  color: #fff;
}

.lighter {
  opacity: 0.6;
}

.blue {
  background: #11aacc !important;
}

.blue::after {
  border-color: #11aacc transparent transparent transparent !important;
}

.gray {
  background: #999 !important;
}

.green {
  background: #77cc11;
}

.white {
  background: #fff !important;
  color: #999 !important;
  border: 1px solid #ccc !important;
  opacity: 1 !important;
}

.white:hover {
  background: #fafafa !important;
}

.light-gray {
  background: #f4f4f4;
}

.dark-gray {
  background: #e3e3e3;
}

.s0-5, .s1, .s2, .s3, .s4, .s5, .s7-5, .s8 {
  display: block;
  width: 100%;
  clear: both;
}

.s0-5 {
  height: 5px;
}

.s1 {
  height: 10px;
}

.s2 {
  height: 20px;
}

.s3 {
  height: 30px;
}

.s4 {
  height: 40px;
}

.s5 {
  height: 50px;
}

.s7-5 {
  height: 75px;
}

.s8 {
  height: 80px;
}

.active {
  color: #11aacc;
  border-color: #11aacc;
}

.hidden {
  display: none !important;
}

.border-top {
  border-top: 1px solid #ccc;
}

.shadow-top {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 2;
  position: relative;
}

.gray-filler {
  position: fixed;
  z-index: -99;
  background: #f4f4f4;
  top: 400px;
  left: 0;
  right: 0;
  bottom: 0;
}

.tab-sticky {
  background: #fff;
  width: 100%;
}

.tab-container {
  display: inline-block;
  max-width: 1010px;
  border-right: 1px solid #ccc;
}

.dark-tab {
  display: inline-block;
  padding: 15px 25px 15px 5px;
  background: #fff;
  font-weight: 700;
  color: #333;
}

.tab {
  display: inline-block;
  padding: 15px 25px;
  border-left: 1px solid #ccc;
  color: #999;
  background: #fff;
}

.tab:hover {
  cursor: pointer;
  background: #f4f4f4;
  color: #11aacc;
}

.active-tab {
  background: #f4f4f4;
  color: #11aacc;
}

.fixed {
  position: fixed;
  top: 0;
  z-index: 3;
  border-bottom: 1px solid #ccc;
}

.extra {
  padding-top: 53px;
}


